import { createSlice } from '@reduxjs/toolkit';
const basicCommon = {
  x:0,
  y:0,
  checkStatus:false,
  boardStatus:false,
  debut:false,
  roundMove:1
};
const chess = createSlice({
  name:'chess',
  initialState:[
    {
      name:'Tanjirou',
      cn:' ',
      step:3,
      attack:2,
      hp:1500,
      sp:125,
      dodge:60,
      fullValue:{
        hp:1500,
        sp:125
      },
      skill:[{ 
        name:'',
        atk:150,
        sp:0,
        hitfix:0
      },{ 
        name:'',
        atk:300,
        sp:15,
        hitfix:10
      },{ 
        name:'',
        atk:400,
        sp:25,
        hitfix:5
      },{
        name:'',
        atk:600,
        sp:50,
        hitfix:20
      }],
      ...basicCommon
    },{
      name:'Nezuko',
      step:2,
      attack:1,
      hp:2200,
      sp:200,
      dodge:50,
      fullValue:{
        hp:2200,
        sp:200
      },
      skill:[{ 
        name:'',
        atk:50,
        sp:0,
        hitfix:0
      },{ 
        name:'',
        atk:120,
        sp:5,
        hitfix:0
      },{ 
        name:'',
        atk:1000,
        sp:100,
        hitfix:0
      }],
      ...basicCommon
    },{
      name:'Inosuke',
      cn:' ',
      step:4,
      attack:3,
      hp:1800,
      sp:80,
      dodge:40,
      fullValue:{
        hp:1800,
        sp:80
      },
      skill:[{ 
        name:'',
        atk:50,
        sp:0,
        hitfix:0
      },{ 
        name:'',
        atk:120,
        sp:2,
        hitfix:0
      },{ 
        name:'',
        atk:200,
        sp:5,
        hitfix:0
      },{
        name:'',
        atk:400,
        sp:8,
        hitfix:0
      }],
      ...basicCommon
    }
  ],
  reducers:{
    stageDebut:(state, actions)=> {
      const { isDebutChess } = actions.payload;
      state.map((value,key)=>{
        return isDebutChess.map(v=>{
          if(value.name===v.get('name')){
            return (
              state[key].debut = true,
              state[key].x = v.get('x'),
              state[key].y = v.get('y')
            );
          }else return null;
        })
      })
    },
    chessMove: (state, actions)=>{
      const { key, changeX, changeY } = actions.payload;
      state[key].x = changeX;
      state[key].y = changeY;
    },
    chessSelected: (state, actions)=>{
      const { key } = actions.payload;
      state[key].boardStatus = !state[key].boardStatus;
    },
    chessCheckStatus: (state, actions)=> {
      const { key } = actions.payload;
      state[key].checkStatus = !state[key].checkStatus;
    },
    chessAttackResult: (state, actions)=> {
      const { key, lessSp } = actions.payload;
      state[key].sp -= lessSp;
    },
    chessMoved: (state, actions)=>{
      const { key } = actions.payload;
      state[key].roundMove -= 0;
    },
  }
});
export default chess.reducer;
export const { 
  stageDebut, chessMove, chessSelected, chessCheckStatus, chessAttackResult, chessMoved
} = chess.actions;